@import url("https://fonts.googleapis.com/css2?family=Overpass:wght@400;700;800&display=swap");

/*Basic Reset*/
html {
  box-sizing: border-box;
  font-size: 62.5%;
}
/* Box Sizing */
*,
*::before,
*::after {
  box-sizing: inherit;
}

img {
  max-width: 100%;
  display: block;
}

:root {
  --very-dark-blue: #131518;
  --dark-blue: #262e38;
  --medium-grey: #7c8798;
  --light-grey: #969fad;
  --orange: #fc7614;
  --pure-white: #fff;
}

body {
  align-items: center;
  background-color: var(--very-dark-blue);
  color: white;
  display: flex;
  height: 100svh;
  justify-content: center;
  margin: 0;
  padding: 0;
  font-family: "Overpss", sans-serif;
}

.container {
  background: radial-gradient(
    98.96% 98.96% at 50% 0%,
    #232a34 0%,
    #181e27 100%
  );
  border-radius: 3rem;
  height: 41.6rem;
  padding: 3.2rem 4rem 3.2rem 3.2rem;
  width: 41.2rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
@media screen and (max-width: 425px) {
  .container {
    width: 30rem;
    padding: 2.4rem 2.4rem 3.2rem;
  }
}
.favorite-icon {
  width: 4.8rem;
  height: 4.8rem;
  border-radius: 50%;
  background-color: var(--dark-blue);
  padding: 1.6rem;
  margin-bottom: 3rem;
}
.thank-you-illustration {
  margin: 0 auto 3.2rem;
}
.ask-for-rating-heading,
.rating-heading {
  font-size: 2.8rem;
}
.ask-for-rating-heading {
  margin-top: 0;
  margin-bottom: 0.7rem;
}
.ask-for-rating-paragraph {
  font-size: 1.5rem;
  line-height: 2.4rem;
  margin: 0 0 2.4rem;
  color: var(--light-grey);
}
.rating-buttons {
  display: flex;
  justify-content: center;
  gap: 2.1rem;
  margin: 0 0 3.2rem;
}
@media screen and (max-width: 425px) {
  .rating-buttons {
    gap: 1.5rem;
  }
}

.rating-buttons > button {
  background-color: var(--very-dark-blue);
  border: none;
  border-radius: 50%;
  width: 5.1rem;
  height: 5.1rem;
  color: var(--medium-grey);
  transition: background-color 0.4s; /* Smooth transition for color change */
  cursor: pointer;
}
@media screen and (max-width: 425px) {
  .rating-buttons > button {
    width: 4rem;
    height: 4rem;
  }
}

/* Hover state */
.rating-buttons > button:hover {
  background-color: var(--orange);
  color: var(--pure-white); /* Assuming you want white text on hover */
}

/* Selected state */
.rating-buttons > button.selected-rating {
  background-color: var(--medium-grey);
  color: var(--pure-white); /* Assuming you want white text when selected */
}

.submit-button {
  background-color: var(--orange);
  border: none;
  border-radius: 2.25rem;
  color: var(--pure-white);
  font-size: 1.5rem;
  font-family: "Overpss", sans-serif;
  letter-spacing: 0.2rem;
  text-transform: uppercase;
  font-weight: 700;
  padding: 1.1rem;
  transition: background-color 0.4s;
}

.submit-button:hover {
  background-color: var(--pure-white);
  color: var(--orange);
  cursor: pointer;
}

.rating-result {
  border-radius: 2.25rem;
  background-color: var(--dark-blue);
  color: var(--orange);
  font-size: 1.5rem;
  line-height: 2.4rem;
  width: 19.3rem;
  margin: 0 auto 3.2rem;
  padding: 0.4rem 2rem;
}

.rating-heading {
  margin: 0 auto 0.7rem;
}

.rating-paragraph {
  margin: 0;
  text-align: center;
  color: var(--light-grey);
  font-size: 1.5rem;
  line-height: 2.4rem;
}
